article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

a {
  &:focus {
    outline: thin dotted;
  }

  &:active,
  &:hover {
    outline: 0;
  }
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}

mark {
  background: #ff0;
  color: #000;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
}

q {
  quotes: '\201C''\201D''\2018''\2019';
}

small {
  font-size: 80%;
}

sub {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

button,
select {
  text-transform: none;
}

button,
html input[type='button'] {
  -webkit-appearance: button;
  cursor: pointer;
}

input {
  &[type='reset'],
  &[type='submit'] {
    -webkit-appearance: button;
    cursor: pointer;
  }
}

button[disabled],
html input[disabled] {
  cursor: default;
}

input {
  &[type='checkbox'],
  &[type='radio'] {
    box-sizing: border-box;
    padding: 0;
  }

  &[type='search'] {
    -webkit-appearance: textfield;
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;

    &::-webkit-search-cancel-button,
    &::-webkit-search-decoration {
      -webkit-appearance: none;
    }
  }
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;

  &::after,
  &::before {
    box-sizing: border-box;
  }
}

body {
  --color-text: #f1f1f1;
  --color-bg: #1d1d1d;
  --color-link: #1ab3de;
  --color-link-hover: #f1f1f1;
  --color-deco: #141414;
  --color-side: #f1f1f1;
  font-family: 'Chivo', sans-serif;
  font-weight: 300;
  min-height: 100vh;
  color: #57585c;
  color: var(--color-text);
  background-color: var(--color-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: var(--color-link);
  outline: none;

  &:hover,
  &:focus {
    color: var(--color-link-hover);
    outline: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  text-transform: uppercase;
}

h5 {
  font-size: 20px;
  margin: 10px 0;
}

h6 {
  font-size: 15px;
  margin: 10px 0;
}

ul {
  margin-top: 0;
}

#icon-navarrow {
  animation: jello 2s infinite;
  transform-origin: center;
}

@keyframes jello {
  11.1% {
    transform: none;
  }

  22.2% {
    transform: skewX(-12.5deg) skewY(-12.5deg);
  }

  33.3% {
    transform: skewX(6.25deg) skewY(6.25deg);
  }

  44.4% {
    transform: skewX(-3.125deg) skewY(-3.125deg);
  }

  55.5% {
    transform: skewX(1.5625deg) skewY(1.5625deg);
  }

  66.6% {
    transform: skewX(-0.78125deg) skewY(-0.78125deg);
  }

  77.7% {
    transform: skewX(0.390625deg) skewY(0.390625deg);
  }

  88.8% {
    transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
  }

  100% {
    transform: none;
  }
}

.animateTxt {
  animation: text-shadow 1.5s ease-in-out infinite;
  font-weight: 900;
  line-height: 1;

  &:hover {
    animation-play-state: paused;
  }
}

@keyframes text-shadow {
  0% {
    transform: translateY(0);
    text-shadow: 0 0 0 #000000, 0 0 0 #000000, 0 0 0 #999999, 0 0 0 #747474;
  }

  20% {
    transform: translateY(-1em);
    text-shadow: 0 0.125em 0 #000000, 0 0.25em 0 #000000, 0 -0.125em 0 #999999,
      0 -0.25em 0 #747474;
  }

  40% {
    transform: translateY(0.5em);
    text-shadow: 0 -0.0625em 0 #000000, 0 -0.125em 0 #000000,
      0 0.0625em 0 #999999, 0 0.125em 0 #747474;
  }

  60% {
    transform: translateY(-0.25em);
    text-shadow: 0 0.03125em 0 #000000, 0 0.0625em 0 #000000,
      0 -0.03125em 0 #999999, 0 -0.0625em 0 #747474;
  }

  80% {
    transform: translateY(0);
    text-shadow: 0 0 0 #000000, 0 0 0 #000000, 0 0 0 #999999, 0 0 0 #747474;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }
}

.hidden {
  position: absolute;
  overflow: hidden;
  width: 0;
  height: 0;
  pointer-events: none;
}

.not-available:hover {
  background-color: black;
  color: black;
  animation: unset;
  z-index: 1;
  box-shadow: 0 0 2px #fff, 0 0 10px #fff, 0 0 20px #1d1d1d, 0 0 30px #1d1d1d,
    0 0 40px #1d1d1d, 0 0 50px #1d1d1d;
  border-radius: 5px;
  padding: 0 10px;
}

.not-available-box {
  display: none;
  flex-direction: column;
  align-items: center;
  color: white;
}

.not-available:hover .not-available-box {
  display: flex;
}

.not-available-box h6 {
  font-family: 'Chivo', sans-serif;
  text-transform: initial;
  font-weight: 700;
  font-size: 18px;
  margin: 10px 0 15px;
}

.mockups {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;

  span {
    height: 1px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
  }

  a {
    color: white;
    margin: 10px 0 13px;
    text-decoration: underline;
    animation-duration: 1.4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    &:hover {
      opacity: 0.5;
    }
  }
}

.ec a {
  animation-name: colorchangeEC;
}

.ze a {
  animation-name: colorchangeZE;
}

@keyframes colorchangeEC {
  0% {
    color: #f4960c;
  }

  1% {
    color: white;
  }

  39% {
    color: white;
  }

  40% {
    color: #f4960c;
  }

  100% {
    color: #f4960c;
  }
}

@keyframes colorchangeZE {
  0% {
    color: #eeee21;
  }

  1% {
    color: white;
  }

  39% {
    color: white;
  }

  40% {
    color: #eeee21;
  }

  100% {
    color: #eeee21;
  }
}

.message {
  position: relative;
  z-index: 100;
  display: none;
  padding: 1em;
  text-align: center;
  color: var(--color-bg);
  background: var(--color-text);
}

.is-racist-slideshow {
  background-color: white;
}

.is-racist-slideshow-content {
  color: grey;
  text-shadow: 0 1px 2px rgba(104, 104, 104, 0.2) !important;
}

/* Icons */

.icon {
  display: block;
  width: 2em;
  height: 2em;
  margin: 0 auto;
  fill: currentColor;
}

.frame {
  position: fixed;
  z-index: 10000;
  top: 5rem;
  left: 0;
  width: 100%;
  max-width: none;
  min-height: 0;
  height: 100vh;
  padding: 1rem;
  pointer-events: none;

  a {
    pointer-events: auto;
  }
}

.slideshow {
  position: relative;
  overflow: hidden;
  margin: 0;
  height: 100vh;
  width: 100%;
  height: calc(100vh - 10rem);
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-column-gap: 0.5%;
  grid-template-rows: 100%;
  grid-template-areas: '... slide ...';
}

.slide {
  width: 100%;
  display: flex;
  pointer-events: none;
  cursor: pointer;
  position: relative;
  height: 100%;
  grid-area: slide;
}

.slideshow--previewopen .slide {
  cursor: default;
}

.slide--current {
  pointer-events: auto;
}

.slide__img-wrap {
  width: 100%;
  overflow: hidden;
  z-index: 100;
  height: 80%;
  top: 10%;
  position: absolute;
}

.slideshow__deco {
  grid-area: slide;
  background: var(--color-deco);
  width: 100%;
  height: 80%;
  align-self: center;
  position: relative;
  margin: -40px 0 0 0;
  right: -20px;
}

.nav {
  position: absolute;
  background: none;
  width: 3rem;
  height: 3rem;
  z-index: 1000;
  border: 0;
  padding: 0;
  margin: 0;
  pointer-events: none;
  transition: transform 0.8s, opacity 0.8s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.nav--next {
  bottom: 1rem;
  right: 1rem;
}

.icon--navarrow-next {
  transform: rotate(45deg);
}

.nav--prev {
  top: 1rem;
  left: 1rem;
}

.icon--navarrow-prev {
  transform: rotate(-135deg);
}

.slideshow--previewopen {
  .nav {
    opacity: 0;
    transition-duration: 0.4s;
  }

  .nav--next {
    transform: translate3d(100%, 100%, 0);
  }

  .nav--prev {
    transform: translate3d(-100%, -100%, 0);
  }
}

.slide__img {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-size: cover;
  background-position: 50% 50%;
  position: absolute;
  pointer-events: none;
  transform: scale3d(1.01, 1.01, 1);
}

.js {
  .slide__img-wrap,
  .slide__title-wrap,
  .slide__side {
    opacity: 0;
    pointer-events: none;
  }

  .slide--current .slide__img-wrap {
    opacity: 1;
    pointer-events: auto;
  }
}

.slide--visible .slide__img-wrap {
  pointer-events: auto;
}

.slide__title-wrap {
  justify-self: flex-end;
  width: 100%;
  position: relative;
  z-index: 1000;
}

.slide__number {
  display: block;
  font-size: 2rem;
  font-weight: bold;

  &::before {
    content: '\2014';
    display: inline-block;
    margin: 0 1rem 0 0;
  }
}

.slide__title,
.slide__subtitle,
.slide__side {
  display: none;
}

.content {
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  height: calc(100% - 10rem);
  pointer-events: none;
  z-index: 100;
}

.content__item {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 2vh 5vw;
  overflow: auto;
}

.content__item--current {
  pointer-events: auto;

  ~ .content__close {
    pointer-events: auto;
  }
}

.content__close {
  position: absolute;
  top: 1rem;
  left: -16rem;
  background: none;
  color: currentColor;
  border: 0;
  margin: 0;
  padding: 0;
}

.content__link {
  position: absolute;
  opacity: 0;
  right: 3rem;
  color: white;
  font-weight: bold;
  text-decoration: underline;
}

.content__date {
  position: absolute;
  opacity: 0;
  font-size: 11px;
  left: 7rem;
  top: 1.1rem;
}

.icon--longarrow {
  width: 2rem;
}

.content__close:focus {
  outline: none;
}

.content__number {
  font-weight: bold;

  &::before {
    content: '\2014';
    display: inline-block;
    margin: 0 1rem 0 0;
  }
}

.content__title {
  margin: 0;
  font-size: 2rem;
}

.content__subtitle {
  margin: 1rem 0;
  font-size: 1.5rem;
  font-weight: bold;
}

.content__text {
  font-size: 0.85rem;
}

.js {
  .content__title,
  .content__subtitle,
  .content__number,
  .content__text,
  .content__close {
    opacity: 0;
  }
}

@media screen and (min-width: 53em) {
  /* body {
        padding: 0;
    } */

  .frame {
    top: 0;
    display: grid;
    align-items: start;
    justify-items: end;
    grid-template-columns: 50% 50%;
    grid-template-rows: 100%;
    grid-template-areas: '... header';
  }

  .slideshow {
    height: 100vh;
    grid-template-columns: 27% 27% 27%;
    grid-column-gap: 9.5%;
  }

  .slide {
    padding: 10vh 0 7vh;
    flex-direction: column;
    justify-content: space-between;
  }

  .slide__side,
  .slide__title-wrap {
    margin: 0 0 0 -1.85rem;
  }

  .slide__title,
  .slide__subtitle,
  .slide__side {
    display: block;
  }

  .slide__title {
    font-size: 3.25rem;
    margin: 0 0 0.25rem;
  }

  .slide__subtitle {
    font-weight: normal;
    margin: 0;
    min-height: 50px;
  }

  .slide__side {
    color: var(--color-side);
    position: relative;
    -webkit-writing-mode: vertical-rl;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    z-index: 1000;
  }

  .content {
    top: 0;
    height: 100%;
    width: 100%;
  }

  .content__item {
    padding: calc(5vh + 5rem) 0 0px;
    width: 50.5%;
    right: 7.5%;
    overflow: visible;
  }

  .content__close {
    left: 42%;
    top: calc(9.5vh + 0rem);
  }

  .content__number {
    position: absolute;
    bottom: 7vh;
    right: 0;
    font-size: 2rem;
  }

  .content__link {
    right: 1rem;
    top: calc(10vh + 0rem);
  }

  .content__date {
    top: calc(11vh + 0rem);
    left: 10rem;
  }

  .content__title {
    /* font-size: 7vw; */
  }

  .content__subtitle {
    /* font-size: 1.15rem;
      margin-bottom: 7.5vh; */
  }

  .content__text {
    font-size: 0.95rem;
    max-width: 600px;
    text-align: justify;
  }
}
